.content_inner.email_camp{
    margin-top: 20px;
    .select-grid{
        border-right: none;
        border-bottom: none;
        // button{
        //     border: var(--bs-border-width) var(--bs-border-style) var(--bs-border-color) !important;
        // }
    }
    .aside_body .select-grid {
        button div{
            background-color: #F6F9FB;
            img{
                max-width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }
        .btn{
            &.active, &:hover{
                background-color: var(--bs-body-bg);
                color: var(--bs-primary);
                border-color: var(--bs-primary) !important;
                img{
                    filter: invert(18%) sepia(33%) saturate(357%) hue-rotate(309deg) brightness(104%) contrast(99%);
                }
                i{
                    color: #fec1c1 !important;
                }
            }
        }
    }
}
.publish-content .full-content {
    padding-bottom: 0;
    .preview_.p_header_ .card-body{
        height: calc(100vh - 165px);
    }
    .container-fluid {
        padding-left: 0;
        padding-right: 0;
    }
}
[data-name=email-campaign-publish]{
    .publish-content .full-content{
        padding-top: 50px;
    }
}
.container-email-campaign {
    padding-top: 0;
    .preview_{
        .card-header{
            .nav-link{
                &:hover{
                    color: var(--bs-dark);
                    background-color: var(--bs-gray-200);
                }
                border: 1px solid var(--bs-gray-500);
                padding: 6px 16px;
                &.active{
                    &:hover, &.active{
                        background-color: rgba(var(--bs-primary-rgb), 0.2);
                        color: var(--bs-primary);
                        border-color: var(--bs-primary);
                    }
                }
            }
        }
    }
    .aside__body .aside_body{
        height: calc(100% - 65px);
        .email-platform{
            height: 132px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
    .select-grid{
        border-right: none;
        border-bottom: none;
        .btn{
            height: 100%;
            &.active, &:hover{
                background-color: var(--bs-body-bg);
                color: var(--bs-primary);
                border-color: var(--bs-primary) !important;
                div img{
                    filter: invert(18%) sepia(33%) saturate(357%) hue-rotate(309deg) brightness(104%) contrast(99%);
                }
            }
            div {
                background-color: #F6F9FB;
                img {
                    max-width: 100%;
                    height: 100%;
                    object-fit: contain;
                }
            }
        }
    }
}
[data-product=tagembed] .container-email-campaign{
    .select-grid{
        .btn{
            &.active, &:hover{
                div img{
                    filter: brightness(0%) saturate(100%) invert(86%) sepia(14%) saturate(645%) hue-rotate(196deg) brightness(97%) contrast(92%);
                }
            }
        }
    }
}
.emailcapaignPreview{
    width: 100%;
    overflow-y: auto;
    height: calc(100vh - 170px);
}

.email_campaign_view{
    height: calc(100vh - 460px);
    min-height: 280px;
    overflow-y: auto;
}

.emailcapaignPreview #emailEmbedCode_{
    word-break: break-all;
    max-height: 300px;
    overflow: auto;
}

.email_gallery .social_icon{
    display: none;
}
.email_camp .aside__ .a_footer_.a_header_ .card-body{
    height: calc(100vh - 205px);
}
@media (min-width: 992px) {
    .emailcapaigncontainer {
        height: calc(100vh - 249px);
        overflow: auto;
        min-height: 465px;
    }
}
@media (min-width: 1280px) {
    .emailcapaignPreview{
        width: 440px;
        // height: calc(100vh - 190px);
        // overflow: auto;
        // min-height: 520px;
    }
    .emailcapaignPreviewWrap {
        height: calc(100vh - 250px);
        overflow: auto;
    }
    .emailcapaigncontainer {
        height: auto;
    }
    .email_gallery .social_icon{
        display: block;
    }
}


